import React from 'react';
import PodiumStep from './PodiumStep.js';
import {getTranslation} from '../../../text/translate';
import './Winners.css';

const Winners = ({language}) => {
    const link = getTranslation(language, 'winners', 'link');

    return (
        <div id={'winners'} className={'page'}>
            <h1 className={'title'}>{getTranslation(language, 'winners', 'title')}</h1>

            <div className={'podium'}>
                <PodiumStep language={language} position={2}/>
                <PodiumStep language={language} position={0}/>
                <PodiumStep language={language} position={1}/>
            </div>

            <p>{getTranslation(language, 'winners', 'kaggle')}
                <a target={'_blank'} href={link}>
                    {link}
                </a>
            </p>
        </div>
    )
}

export default Winners;
