import React from 'react';
import Card from 'react-bootstrap/Card';
import {getTranslation} from '../../../text/translate';
import './Conferences.css';


const ConferenceCard = (props) => {
    const card = props.card;

    return (
        <Card text={'dark'}>
            <Card.Header className={'text-muted'}>{card.time}</Card.Header>

            <Card.Body>
                <Card.Title>{card.title}</Card.Title>
                <Card.Subtitle><Card.Link className={'text-muted'} href={card.contact}>
                    {card.speaker}
                </Card.Link></Card.Subtitle>
                <Card.Text>{card.details}</Card.Text>
            </Card.Body>
        </Card>
    )
}

const Conferences = (props) => {
    return (
        <div id={'conferences'} className={'page'}>
            <h2 className={'date'}>{getTranslation(props.language, 'conferences', 'date')}</h2>
            <h1 className={'title'}>{getTranslation(props.language, 'conferences', 'title')}</h1>
            <ul>
                {getTranslation(props.language, 'conferences', 'cards').map((item, index) =>
                    <ConferenceCard key={'coferenceCard' + index} card={item}/>
                )}
            </ul>

            <p>{getTranslation(props.language, 'conferences', 'endDescription')}</p>
        </div>
    )
}

export default Conferences;
