import React from 'react';
import Card from 'react-bootstrap/Card';
import {getTranslation} from '../../../text/translate';
import './Workshops.css';

const WorkshopCard = (props) => {
    const card = props.card;

    return (
        <Card>
            <Card.Header className={'text-muted'}>{card.date}, {card.time}</Card.Header>

            <Card.Body>
                <Card.Title>{card.title}</Card.Title>
                <Card.Subtitle className={'text-muted'}>{card.speakers}</Card.Subtitle>
                <Card.Text>{card.description}</Card.Text>
            </Card.Body>

            <Card.Footer className="text-muted">
                {card.link !== '#' && <Card.Link href={card.link}>{card.link}</Card.Link>}
                {card.link === '#' && 'N/A'}
            </Card.Footer>
        </Card>
    )
}

const Workshops = (props) => {
    return (
        <div id={'workshops'} className={'page'}>
            <h2 className={'date'}>{getTranslation(props.language, 'workshops', 'date')}</h2>
            <h1 className={'title'}>{getTranslation(props.language, 'workshops', 'title')}</h1>
            <ul className={'details'}>
                {getTranslation(props.language, 'workshops', 'details').map((item, index) =>
                    <li key={'detail' + index}>{item}</li>
                )}

                <p>{getTranslation(props.language, 'workshops', 'github')}
                    <a target={'_blank'} href={'https://github.com/Nitro-Language-Processing/Workshops'}>
                        https://github.com/Nitro-Language-Processing/Workshops
                    </a>
                </p>
            </ul>

            <ul className={'cards'}>
                {getTranslation(props.language, 'workshops', 'cards').map((item, index) =>
                    <WorkshopCard key={'workshopCard' + index} card={item}/>
                )}
            </ul>

            <h1 className={'title'}>{getTranslation(props.language, 'workshops', 'inviteTitle')}</h1>
            <ul className={'cards inviteCards'}>
                {getTranslation(props.language, 'workshops', 'inviteCards').map((item, index) =>
                    <WorkshopCard key={'inviteCard' + index} card={item}/>
                )}
            </ul>
        </div>
    )
}

export default Workshops;
