import React from 'react';
import {motion} from 'framer-motion'
import WinnerCard from './WinnerCard.js';
import {getTranslation} from '../../../text/translate';

export default function PodiumStep({language, position}) {
    const offset = 3 - position
    const delta = 2

    return (
        <div className={'podium-step'}>
            <motion.div
                style={{
                    alignSelf: 'center',
                    marginBottom: '.2rem'
                }}
                initial='hidden'
                animate='visible'
                variants={{
                    visible: {
                        opacity: 1,
                        transition: {
                            delay: offset - delta + 2,
                            duration: 0.75
                        }
                    },
                    hidden: {opacity: 0}
                }}
            >
                <WinnerCard
                    winners={getTranslation(language, 'winners', 'prize' + String(position + 1))}
                />
            </motion.div>

            <motion.div
                style={{
                    width: '20vw',
                    placeContent: 'center',
                    alignSelf: 'center',
                    display: 'flex',
                    borderTopLeftRadius: '.5rem',
                    borderTopRightRadius: '.5rem',
                    borderColor: '#401365',
                    backgroundColor: '#401365',
                    marginBottom: 0,
                    filter: `opacity(${0.1 + offset / 3})`
                }}
                initial='hidden'
                animate='visible'
                variants={{
                    visible: {
                        height: 200 * (offset / 3),
                        opacity: 1,
                        transition: {
                            delay: offset - delta + 1,
                            duration: 2,
                            ease: 'backInOut'
                        }
                    },
                    hidden: {opacity: 0, height: 0}
                }}
            >
        <span style={{color: 'white', alignSelf: 'flex-end'}}>
          {position + 1}
        </span>
            </motion.div>
        </div>
    )
}
