import React, {useState} from 'react';

const WinnerCard = (props) => {
    const winner = props.winners;
    const [isClicked, setIsClicked] = useState(false);

    return (
        <button className={'winner-card ' + props.className} onClick={() => setIsClicked(!isClicked)}>
            <h3 className={'winner-team'}>{winner.team}</h3>

            {isClicked && <ul className={'winner-members'}>
                {winner.members.map((member, index) => <li key={winner.title + '-member-' + index}
                                                           className={'center'}>{member}</li>)}
            </ul>}

            {/*<ul>*/}
            {/*    {winner.prizes.map((prize, index) =>*/}
            {/*        <li key={winner.title + '-prize-' + index} className={'center'}>{prize}</li>*/}
            {/*    )}*/}
            {/*</ul>*/}
        </button>
    )
}

export default WinnerCard;
